import React, { useCallback, useEffect, useRef, useState } from 'react';
import { Feature } from '../../../../../app/features/types/Feature';
import styles from './FeatureToggler.module.css';
import { featureStore } from '@app/features/list';
import { useFeaturesList } from '@app/features/hooks/useFeaturesList';
import { browser_api } from '@app/browser_api';
import { useLocalStorage } from '@popup/shared/hooks/useLocalStorage';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import { deleteLocalStorageData } from '@app/storage/local/shared';

const isDevModeFromEnv = process.env.DEV_MODE === 'true';

export const FeatureToggler: React.FC = () => {
  const { featuresList: features } = useFeaturesList();
  const [isVisible, setIsVisible] = useState(false);

  const [isDevMode, setIsDevMode] = useLocalStorage(
    LocalStorageKey.IsDevMode,
    isDevModeFromEnv,
  );

  const isDevModeChanged = useRef(false);

  const { handleLogout } = usePremiumData();

  const toggleFeature = useCallback((key: Feature, isActive: boolean) => {
    featureStore.setFeatureValues({ [key]: isActive });
  }, []);

  useEffect(() => {
    Object.defineProperty(window, 'magicToggler', {
      configurable: true,
      get() {
        setIsVisible(true);
        return 'Feature toggler is now visible!';
      },
    });

    return () => {
      // @ts-ignore
      delete window.magicToggler;
    };
  }, []);

  const applyChanges = async () => {
    if (isDevModeChanged.current) {
      await handleLogout();
    }

    browser_api.runtime.reload();
  };

  const handleReset = async () => {
    featureStore.reset();

    if (isDevModeFromEnv !== isDevMode) {
      isDevModeChanged.current = true;
    }

    // We need to reset isDevMode to undefined as a sign that we don't set it by hand
    await deleteLocalStorageData(LocalStorageKey.IsDevMode);
    await applyChanges();
  };

  const handleIsDevModeChange = async () => {
    await setIsDevMode(!isDevMode);
    isDevModeChanged.current = true;
  };

  if (!isVisible || !features) {
    return null;
  }

  return (
    <div className={styles.overlay}>
      <div className={styles.container}>
        <h3>Feature Toggler</h3>
        <div className={styles.devMode}>
          <label>
            <input
              disabled={isDevModeFromEnv}
              type="checkbox"
              checked={!!isDevMode}
              onChange={handleIsDevModeChange}
            />
            Dev Mode Here
          </label>
        </div>
        <div className={styles.features}>
          {Object.entries(features).map(([feature, isActive]) => (
            <label key={feature} className={styles.item}>
              <input
                type="checkbox"
                checked={features[feature as Feature]}
                onChange={() => toggleFeature(feature as Feature, !isActive)}
              />
              {feature}
            </label>
          ))}
        </div>
        <button className={styles['apply-button']} onClick={applyChanges}>
          
        </button>
        <button className={styles['apply-button']} onClick={handleReset}>
          
        </button>
      </div>
    </div>
  );
};
