import { Logo } from './Logo';
import { LanguageToggler } from './LanguageToggler';
import { TelegramPromo } from '@popup/shared/components/TelegramPromo';

import styles from './Header.module.css';
import clsx from 'clsx';
import { useLocalStorage } from '@popup/shared/hooks/useLocalStorage';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export function Header(): JSX.Element {
  const [isDevMode] = useLocalStorage(LocalStorageKey.IsDevMode, false);

  return (
    <div className={clsx(styles.header, { [styles.devMode]: isDevMode })}>
      <Logo />
      {isDevMode && (
        <label className={styles.devModeLabel}>
          <span> DEV MODE </span>
        </label>
      )}
      <TelegramPromo />
      <LanguageToggler />
    </div>
  );
}
