import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { isMessageOfType } from '../../../../app/messages/shared';
import { Actions } from '../../../../app/actions/shared';
import { RoutePath } from '../routes/types/RoutePath';
import { browser_api } from '@app/browser_api';
import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export function useNavigateToPremiumInfo(): void {
  const navigate = useNavigate();

  useEffect(() => {
    const listener = async (message: unknown) => {
      if (isMessageOfType(message, Actions.NavigateToFreeTrial)) {
        const { isFreePremiumTrialAvailable } = await getLocalStorageData([
          LocalStorageKey.IsFreePremiumTrialAvailable,
        ]);
        if (isFreePremiumTrialAvailable) {
          navigate(RoutePath.PremiumInfo);
        } else {
          navigate(RoutePath.Tariffs);
        }
      }
    };

    browser_api.runtime.onMessage.addListener(listener);
    return () => {
      browser_api.runtime.onMessage.removeListener(listener);
    };
  }, [navigate]);
}
