import { useEffect } from 'react';
import { browser_api } from '@app/browser_api';
import { getLocalStorageData } from '@app/storage/local/shared';
import { getSubscriptionInfo } from '@utils/getSubscriptionInfo';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { usePremiumData } from './usePremiumData';
import { OnboardingStep } from '@popup/project/youboost-neo/components/Onboarding/ui/constants';

export function useCorrectUserId() {
  const { handleLogout } = usePremiumData();

  useEffect(() => {
    const clearAllLocalAndCookieStorage = async (): Promise<void> => {
      await handleLogout();

      browser_api.runtime.reload();
    };

    async function checkUserIdFromStorage() {
      try {
        const { userId } = await getLocalStorageData(LocalStorageKey.UserId);

        if (userId) {
          await getSubscriptionInfo();
        }
      } catch (error) {
        if (error instanceof Error && error.message === 'userNotFound') {
          const { onboardingScreenStatus } = await getLocalStorageData(
            LocalStorageKey.OnboardingScreenStatus,
          );
          if (onboardingScreenStatus === OnboardingStep.Done) {
            await clearAllLocalAndCookieStorage();
          }
        }
      }
    }

    checkUserIdFromStorage();
  }, [handleLogout]);
}
