import { Actions } from '@app/actions/shared';
import { isMessageOfType } from '@app/messages/shared';
import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { RoutePath } from '../routes/types/RoutePath';
import { browser_api } from '@app/browser_api';

export function useNavigateWhenSubscriptionExpiring(): void {
  const navigate = useNavigate();

  useEffect(() => {
    const handleMessage = async (message: unknown) => {
      if (isMessageOfType(message, Actions.ShowSubscriptionExpiringScreen)) {
        if (message.payload.status === 'buy-subscription') {
          navigate(RoutePath.Tariffs);
        } else {
          navigate(RoutePath.FreeInfoPersuasion);
        }
      }
    };

    browser_api.runtime.onMessage.addListener(handleMessage);

    return () => {
      browser_api.runtime.onMessage.removeListener(handleMessage);
    };
  }, [navigate]);
}
