import { useEffect, useLayoutEffect, useState } from 'react';
import { type Theme } from '../ThemeContext';
import { usePaymentStatus } from '@popup/shared/hooks/usePaymentStatus';

import { PaymentStatus } from '@app/storage/local/types/persistentTypes';

export function useThemeContext(): {
  theme: Theme;
  isThemeLoading: boolean;
} {
  const [theme, setTheme] = useState<Theme>('premium');
  const [isThemeLoading, setIsThemeLoading] = useState(true);
  const paymentStatus = usePaymentStatus();

  useEffect(() => {
    if (paymentStatus !== undefined) {
      setTheme(paymentStatus === 'Free' ? 'default' : 'premium');
      setIsThemeLoading(false);
    }
  }, [paymentStatus]);

  useEffect(() => {
    const code = process.env.EXTENSION_CODE;
    if (code === 'youboost' && paymentStatus === undefined) {
      setTheme('default');
      setIsThemeLoading(false);
    }
  }, [paymentStatus]);

  useLayoutEffect(() => {
    document.body.classList.toggle('premium', theme === 'premium');
    /*       'data-theme' */
    document.documentElement.setAttribute('data-theme', theme);
  }, [theme]);

  return { theme, isThemeLoading };
}
