import type { Cookies } from 'webextension-polyfill';
import { browser_api } from '@app/browser_api';

const COOKIE_OPTIONS = {
  path: '/',
  secure: true,
  sameSite: 'strict' as Cookies.SameSiteStatus,
  expirationDate: Math.floor(Date.now() / 1000) + 365 * 24 * 60 * 60, // 1 year
};

export enum CookieKey {
  UserId = 'userId',
  DeviceId = 'deviceId',
}

export async function setCookieData(
  data: {
    [key: string]: string;
  },
  cookieDomain: string,
): Promise<void> {
  const entries = Object.entries(data);

  for (const [key, value] of entries) {
    try {
      await browser_api.cookies.set({
        url: `https://${cookieDomain}`,
        name: key,
        value: value,
        ...COOKIE_OPTIONS,
      });
    } catch (error) {
      console.error(`Failed to set cookie for key: ${key}`, error);
      throw error;
    }
  }
}

export async function getCookieData(
  key: CookieKey,
  cookieDomain: string,
): Promise<{ [key: string]: string | null }> {
  try {

    const cookie = await browser_api.cookies.get({
      url: `https://${cookieDomain}`,
      name: key,
    });

    if (!cookie) {
      return { [key]: null };
    }

    return { [key]: cookie.value };
  } catch (error) {
    console.error(`Failed to get cookie for key: ${key}`, error);
    throw error;
  }
}

export async function removeCookieData(
  key: CookieKey | CookieKey[],
  cookieDomain: string,
): Promise<void> {
  try {
    if (Array.isArray(key)) {
      await Promise.all(
        key.map((k) =>
          browser_api.cookies.remove({
            url: `https://${cookieDomain}`,
            name: k,
          }),
        ),
      );
    } else {
      await browser_api.cookies.remove({
        url: `https://${cookieDomain}`,
        name: key,
      });
    }
  } catch (error) {
    console.error(`Failed to remove cookie for key: ${key.toString()}`, error);
    throw error;
  }
}
