import { DeviceLimit } from '@api/urls';
import { CookieKey, removeCookieData } from '@app/storage/cookies/shared';
import {
  deleteLocalStorageData,
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { useCallback, useEffect, useState } from 'react';
import { browser_api } from '@app/browser_api';
import { PaymentStatus } from '@app/storage/local/types/persistentTypes';
import {
  LocalStorageData,
  LocalStorageKey,
} from '@app/storage/local/types/sharedTypes';
import { Actions } from '@app/actions/shared';
import { getDeviceId } from '@app/storage/utils/getDeviceId';
import { DeactivateDeviceMessage } from '@app/messages/shared';
import { getMetaApiData } from '@utils/metaApiData';
import { ConstantStorageKey } from '@app/storage/local/types/constantTypes';

export type PremiumDataHookValues = {
  userId?: string;
  premiumDaysLeft?: number;
  secondsLeft?: number;
  hasPremium?: boolean;
  deviceLimit?: DeviceLimit;
  activeDevices?: number;
  paidUntil: LocalStorageData['paidUntil'];
  isError: boolean;
  isLoading: boolean;
  handleLogout: () => Promise<void>;
};

export function usePremiumData(): PremiumDataHookValues {
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [premiumDaysLeft, setPremiumDaysLeft] = useState<number>();
  const [userId, setUserId] = useState<string>();
  const [deviceLimit, setDeviceLimit] = useState<number>();
  const [activeDevices, setActiveDevices] = useState<number>();
  const [hasPremium, setHasPremium] = useState<boolean>();
  const [isError, setIsError] = useState<boolean>(false);
  const [premiumSecondsLeft, setPremiumSecondsLeft] = useState<
    number | undefined
  >();
  const [paidUntil, setPaidUntil] =
    useState<PremiumDataHookValues['paidUntil']>();

  const clearAllAlarms = useCallback(async (): Promise<void> => {
    await browser_api.alarms.clearAll();
  }, []);

  const clearLocalStorageData = useCallback(async (): Promise<void> => {
    for (const key of Object.values(LocalStorageKey)) {
      if (
        !Object.values(ConstantStorageKey).includes(key as ConstantStorageKey)
      ) {
        await deleteLocalStorageData([key]);
      }
    }
  }, []);

  const unsetPacScript = useCallback(async (): Promise<void> => {
    await browser_api.proxy.settings.clear({ scope: 'regular' });
  }, []);

  const clearCookies = useCallback(async (): Promise<void> => {
    const { cookieDomain } = await getMetaApiData();
    await removeCookieData([CookieKey.UserId], cookieDomain);
  }, []);

  const handleLogout = useCallback(async (): Promise<void> => {
    if (userId) {
      const deviceId = await getDeviceId();
      await browser_api.runtime.sendMessage<DeactivateDeviceMessage>({
        action: Actions.DeactivateDevice,
        payload: {
          userId,
          deviceId,
        },
      });
    }

    await clearLocalStorageData();
    await unsetPacScript();
    await clearCookies();
    await clearAllAlarms();

    await setLocalStorageData({ onboardingScreenStatus: 'step-zero' });

    setUserId(undefined);
    setPremiumDaysLeft(undefined);
    setHasPremium(undefined);
    setIsError(false);
    setPremiumSecondsLeft(undefined);
  }, [
    clearAllAlarms,
    clearCookies,
    clearLocalStorageData,
    unsetPacScript,
    userId,
  ]);

  useEffect(() => {
    async function getPremiumInfo(): Promise<void> {
      setIsLoading(true);
      const {
        userId: id,
        daysLeft,
        hasPremium: hasPremiumCurrently,
        isSubscriptionInfoError,
        status,
        secondsLeft,
        deviceLimit,
        activeDevices,
        paidUntil,
      } = await getLocalStorageData([
        LocalStorageKey.UserId,
        LocalStorageKey.DaysLeft,
        LocalStorageKey.HasPremium,
        LocalStorageKey.IsSubscriptionInfoError,
        LocalStorageKey.Status,
        LocalStorageKey.SecondsLeft,
        LocalStorageKey.DeviceLimit,
        LocalStorageKey.ActiveDevices,
        LocalStorageKey.PaidUntil,
      ]);
      setUserId(id);
      setPremiumDaysLeft(daysLeft);
      setHasPremium(Boolean(hasPremiumCurrently));
      setIsError(Boolean(isSubscriptionInfoError));
      setPremiumSecondsLeft(secondsLeft);
      setDeviceLimit(deviceLimit);
      setActiveDevices(activeDevices);
      setPaidUntil(paidUntil);
      setIsLoading(false);
    }

    void getPremiumInfo();
  }, []);

  useEffect(() => {
    function listener(changes: {
      paidUntil?: { newValue?: string };
      daysLeft?: { newValue?: number };
      secondsLeft?: { newValue?: number };
      hasPremium?: { newValue?: boolean };
      isSubscriptionInfoError?: { newValue?: boolean };
      status?: { newValue?: PaymentStatus };
      deviceLimit?: { newValue?: number };
      activeDevices?: { newValue?: number };
    }): void {
      if (changes.paidUntil) {
        setPaidUntil(changes.paidUntil.newValue);
      }
      if (changes.daysLeft) {
        setPremiumDaysLeft(changes.daysLeft.newValue);
      }
      if (changes.secondsLeft) {
        setPremiumSecondsLeft(changes.secondsLeft.newValue);
      }
      if (changes.hasPremium) {
        setHasPremium(Boolean(changes.hasPremium.newValue));
      }
      if (changes.isSubscriptionInfoError) {
        setIsError(Boolean(changes.isSubscriptionInfoError.newValue));
      }
      if (changes.deviceLimit) {
        setDeviceLimit(changes.deviceLimit.newValue);
      }
      if (changes.activeDevices) {
        setActiveDevices(changes.activeDevices.newValue);
      }
    }

    browser_api.storage.onChanged.addListener(listener);
    return () => {
      browser_api.storage.onChanged.removeListener(listener);
    };
  }, []);

  useEffect(() => {
    function listener(changes: { userId?: { newValue?: string } }): void {
      if (changes.userId) {
        setUserId(changes.userId.newValue);
      }
    }

    browser_api.storage.onChanged.addListener(listener);
    return () => {
      browser_api.storage.onChanged.removeListener(listener);
    };
  }, []);

  return {
    isLoading,
    hasPremium,
    userId,
    premiumDaysLeft,
    isError,
    secondsLeft: premiumSecondsLeft,
    handleLogout,
    deviceLimit,
    activeDevices,
    paidUntil,
  };
}
